<script>
export default {
  data () {
    return {
      text1: `<y-button type="primary" @click="handleInfoClick">提示</y-button>
<y-button type="warning" @click="handleWarningClick">警告</y-button>
<y-button type="success" @click="handleSuccessClick">成功</y-button>
<y-button type="error" @click="handleErrorClick">错误</y-button>
methods: {
    handleInfoClick () {
      this.$Message.info({ message: 'This is a mormal message', closeable: true })
    },
    handleWarningClick () {
      this.$Message.warning({ message: 'This is a warning message', closeable: true })
    },
    handleSuccessClick () {
      this.$Message.success({ message: 'This is a success message', closeable: true })
    },
    handleErrorClick  () {
      this.$Message.error({ message: 'This is a erro message', closeable: true })
    },
  },`,
      text2: `<y-button type="primary" @click="handleInfoClick2">提示</y-button>
methods: {
    handleInfoClick2 () {
      this.$Message.info({ message: 'This is a mormal message', duration: 3000 })
    },
  },`,
    }
  },
  methods: {
    handleInfoClick2 () {
      this.$Message.info({ message: 'This is a mormal message', duration: 3000 })
    },
    handleInfoClick () {
      this.$Message.info({ message: 'This is a mormal message', closeable: true })
    },
    handleWarningClick () {
      this.$Message.warning({ message: 'This is a warning message', closeable: true })
    },
    handleSuccessClick () {
      this.$Message.success({ message: 'This is a success message', closeable: true })
    },
    handleErrorClick  () {
      this.$Message.error({ message: 'This is a erro message', closeable: true })
    },
  },
}
</script>
